<template>
  <v-snackbar
    :timeout="10000"
    v-model="confirm"
    top
    multi-line
    persistent
    color="error"
    dark
    icon="mdi-alert-circle-outline"
    transition="scale-transition"
    >ì •ë§ ì‚­ì œ í•˜ì‹œê² ìŠµë‹ˆê¹Œ?
    <v-btn color="error" @click="confirmYes" min-width="80">ë„¤</v-btn>
    <v-btn color="error" @click="confirmNo" min-width="80">ì•„ë‹ˆìš”</v-btn>
  </v-snackbar>
</template>
<script>
import axios from "axios";
import eventBus from "./eventBus.js";

export default {
  data() {
    return {
      confirm: false,
      comentId: 0,
      comments: [],
      selected: [],
      alertText: "",
      alert: false,
      CommentORPost: ""
    };
  },
  mounted() {
    eventBus.$on("MoveBin", (item, comments) => {
      this.commentId = item;
      this.comments = comments;
      this.confirm = true;
      this.CommentORPost = "Comment";
    });
    eventBus.$on("triggerDelPost", item => {
      this.boardId = item;
      this.confirm = true;
      this.CommentORPost = "Post";
    });
    eventBus.$on("triggerSelect", item => {
      this.selected = item;
      this.confirm = true;
      this.CommentORPost = "deleteAll";
    });
  },
  methods: {
    confirmYes() {
      if (this.CommentORPost === "Comment") {
        axios
          .delete("/api/board/comment", {
            data: { id: this.commentId }
          })
          .then(r => {
            let i = 0;
            for (i = 0; i < this.comments.length; i++) {
              if (this.comments[i].id === this.commentId) {
                this.comments.splice(i, 1);
              }
            }
            this.confirm = false;
            eventBus.$emit("triggerDelCommentOk", this.comments);
          })
          .catch(e => {
            this.alertText = "ì—ëŸ¬ê°€ ë°œìƒí–ˆìŠµë‹ˆë‹¤";
            this.alert = true;
            console.error(e.message);
          });
      } else if (this.CommentORPost === "Post") {
        axios
          .delete("/api/board", {
            data: { boardId: this.boardId }
          })
          .then(r => {
            location.href = "/board";
          })
          .catch(e => {
            this.alertText = "ì—ëŸ¬ê°€ ë°œìƒí–ˆìŠµë‹ˆë‹¤";
            this.alert = true;
            console.error(e.message);
          });
      } else {
        axios
          .delete("/api/board/deleteAll", {
            data: { posts: this.selected }
          })
          .then(r => {
            location.href = "/board";
          })
          .catch(e => {
            this.alertText = "ì—ëŸ¬ê°€ ë°œìƒí–ˆìŠµë‹ˆë‹¤";
            this.alert = true;
            console.error(e.message);
          });
      }
    },
    confirmNo() {
      this.confirm = false;
    }
  }
};
</script>
